import React, {useEffect} from 'react';
import {PropTypes} from 'prop-types';
import {CONFIG} from '../../config.js';
import {GeoMap} from '../../helpers/geo-map/index.js';
import {GeoMapLayerCountries} from '../../helpers/geo-map/layer/countries/index.js';
import {MapLayerIslandMarker} from './layer/island-marker.js';
import {GeoMapProjectionMiller} from '../../helpers/geo-map/projection/miller.js';
import {useColors} from './use_colors.js';
import {GeoMapLayerBackground} from '../../helpers/geo-map/layer/background/index.js';
import {ModelContour} from './model-contour.js';
import {MapLayerFadeout, getMapLayerFadeoutDefs} from './layer/fadeout.js';
import colors from '../../assets/variables.module.scss';
import {useSize} from '../../helpers/use_size.js';
import {useStateContext} from '../../helpers/state-context.js';
import {useData} from '../../model/use_data.js';
import {useCountryDetails} from '../../model/use_country_details.js';
import {useCountryData} from './use_country_data.js';
import {LocationMarker} from './location-marker.js';
import {LayerTooltip} from './layer-tooltip.js';
import {parseCSSColor} from '../../helpers/color.js';

const Map = function(props){
    const [{selected, hovered}, dispatch] = useStateContext(['selected', 'hovered']);
    const active = selected || (hovered && hovered.type !== 'iso3' ? hovered : null);
    const {getItem} = useData();
    const {getCountryDetails} = useCountryDetails();
    const items = active ? getItem(active.type) : null;
    const activeItem = items ? items[active.index - 1] || null : null;

    const [mapHeight, measuredRef] = useSize((w, h) => (h), 200);
    useEffect(() => {
        props.onHeightChange(mapHeight);
    }, [mapHeight]);
    const bounds = CONFIG.africaArea;

    const {getCountryColor} = useColors(activeItem);
    const {getCountryData, africaFilter, isInFilter} = useCountryData();

    const onCountryClick = (iso3) => {
        // If the country has data go to the country page
        console.log(iso3);
    };

    const setHover = (item) => {
        if(props.isMobile){
            return;
        }
        if(! item?.country_iso3 && hovered && hovered.type === 'iso3'){
            dispatch({type: 'hovered', value: null});
        }else if(item?.country_iso3 && ! active &&
                (! hovered || hovered.type !== 'iso3' || hovered.value !== item.country_iso3)){
            dispatch({type: 'hovered', value: {type: 'iso3', value: item.country_iso3}});
        }
    };

    const contourLayer = (prps, filter = [], sendModalPosition = false) => {
        const getData = (model) => {
            const resultSet = model.getData(getCountryData, filter);
            if(activeItem?.countries && resultSet.bounds && prps.zoom){
                // get the boundingbox, get bottom center and convert to screen position
                const imgBounds = [
                    prps.zoom.getPointFromGeolocation(resultSet.bounds[0]),
                    // cut of the area at the buttom of Africa and ignore some (non-visible) islands somewhere
                    // in the ocean in the south
                    prps.zoom.getPointFromGeolocation(
                            [resultSet.bounds[1][0], Math.max(-35, resultSet.bounds[1][1])])
                ];
                let x = 0.5 * (imgBounds[0][0] + imgBounds[1][0]);
                const y = Math.max(imgBounds[0][1], imgBounds[1][1]);
                // If we also have a location marker, we align the arrow point with that location. We keep the
                // height base on the countries to make sure all countries in this region are visible
                if(activeItem?.location){
                    const markerPos = prps.zoom.getPointFromGeolocation(activeItem.location);
                    x = markerPos[0];
                }
                if(props.modalPosition[0] !== x || props.modalPosition[1] !== y){
                    setTimeout(() => {
                        props.setModalPosition([x, y]);
                    }, 0);
                }
            }
            return resultSet;
        };
        return <GeoMapLayerCountries {...prps}
                getStyle={(id, hoverState, zoomScale) => {
                    return {
                        stroke: 'rgba(0, 0, 0, 0.2)',
                        strokeWidth: (activeItem && activeItem.countries ? 8 : 12) * (1 / zoomScale),
                        fill: 'none'
                    };
                }}
                modelShapes={ModelContour}
                modelShapesFilter={getData}/>;
    };

    const highLightFilter = activeItem?.countries ?
            activeItem?.countries.map((iso3) => {return {iso3};}) :
            africaFilter;

    const getAcdegColor = (status, fact = 1, shift = 0) => {
        if(! status){
            return colors.colorMap;
        }
        const acdegRatio = status === CONFIG.acdegStatus.RATIFIED ?
                0 : (status === CONFIG.acdegStatus.SIGNED ? 0.1 : 0.2);
        const acdegColor = parseCSSColor(colors.colorMap).map((i) => ((1 + shift - fact * acdegRatio) * i));
        return 'rgb(' + acdegColor.slice(0, 3).join(', ') + ')';
    };

    // Setup the map layers (these will be rendered inside the SVG)
    const mapLayers = [
        // browsers sometimes miss the mouseOut event on small shapes, the background in this case is added
        // mainly to reset the hover state when the mouse is over the background.
        (props) => (
            <GeoMapLayerBackground {...props}
                    getStyle={(zoomScale) => {
                        return {
                            fill: colors.colorDark
                        };
                    }}/>
        ),
        // Standard country layer with custom color styling (based on data)
        (props) => (
            <GeoMapLayerCountries {...props}
                    getStyle={(iso3, hoverState, zoomScale) => {
                        const fillColor = getCountryColor(iso3, hoverState);
                        const strokeColor = isInFilter(iso3, africaFilter) ? '#E3B6C8' : '#836B7A';
                        return {
                            stroke: strokeColor,
                            strokeWidth: 0.5 * (1 / zoomScale),
                            fill: fillColor
                        };
                    }}
                    enableHover={true}
                    onClick={onCountryClick}/>
        ),
        // Dropshadow contour under highlighted region
        (props) => (contourLayer(props, highLightFilter)),
        // Highlight area
        (props) => (
            <GeoMapLayerCountries {...props}
                    getStyle={(iso3, hoverState, zoomScale) => {
                        let color = colors.colorMap;
                        if(! activeItem?.countries){
                            const details = getCountryDetails(iso3);
                            color = getAcdegColor(details?.acdeg_status);
                        }
                        // const fillColor = hoverState === 1 ? colors.colorHighlight : color;
                        const fillColor = hovered?.value === iso3 ? colors.colorHighlight : color;
                        const strokeColor = '#E3B6C8';
                        return {
                            stroke: strokeColor,
                            strokeWidth: 0.5 * (1 / zoomScale),
                            fill: fillColor
                        };
                    }}
                    modelShapesFilter={(m) => (m.getData(getCountryData, highLightFilter))}
                    enableHover={true}
                    onClick={onCountryClick}/>
        ),
        // Cicles around small islands
        (props) => (
            <MapLayerIslandMarker {...props}
                    getStyle={(iso3, hoverState, zoomScale) => {
                        const strokeColor = getCountryColor(iso3, hoverState);
                        return {
                            stroke: strokeColor,
                            strokeWidth: 1,
                            fill: 'transparent'
                        };
                    }}
                    enableHover={true}
                    onClick={onCountryClick}/>
        ),
        // Fade-out at the edges of the map
        (prps) => {
            const directions = {
                top: true,
                bottom: ! props.isMobile,
                left: ! props.isMobile,
                right: ! props.isMobile
            };
            const fadeWidthDesktop = {
                top: props.headerHeight,
                bottom: 200,
                left: 200,
                right: 200
            };
            return <MapLayerFadeout {...prps} fadeWidth={props.isMobile ? 100 : fadeWidthDesktop}
                    directions={directions}/>;
        },
        // location marker
        (prps) => (
            activeItem && activeItem.location ?
                <LocationMarker {...prps} coordinates={activeItem.location} itemIndex={parseInt(active.index)}
                        setModalPosition={activeItem.countries ? () => {} : props.setModalPosition}
                        modalPosition={props.modalPosition}/> :
                null
        )
    ];

    // A custom layer (html) to add to the map. This will show a tooltip when hovering over a country.
    const htmlLayers = [
        (props) => (
            <LayerTooltip {...props}
                    getTooltipText={(iso3) => {
                        if(hovered?.type !== 'iso3'){
                            return null;
                        }
                        const details = getCountryDetails(iso3);
                        if(! details){
                            return null;
                        }
                        const recItems = getItem('recs')?.filter((item) => (
                            item.countries.includes(iso3)
                        ));
                        const recs = (recItems || []).map((item) => (item['title-short']));
                        return <dl>
                            <dt>REC membership</dt>
                            <dd>{recs.length === 0 ? 'None' : recs.join(', ')}</dd>
                            <dt>ACDEG adoption</dt>
                            <dd>
                                {(CONFIG.acdegStatusTitles[details?.acdeg_status] || 'No data') +
                                (details?.acdeg_update_year ? ' (' + details.acdeg_update_year + ')' : '')}
                            </dd>
                        </dl>;
                    }}
                    getTooltipTitle={(iso3, countryName) => {
                        const details = getCountryDetails(iso3);
                        return details.country || countryName;
                    }}/>
        )
    ];

    // Setting up the zoom settings for the map
    const zoomSettings = {
        enabled: false,
        enableControls: false,
        zoomRange: [1, 7],
        bounds,
        margin: props.isMobile ?
            {top: 20, right: 0, left: 0, bottom: 20} :
            {top: props.headerHeight, right: 300, left: 300, bottom: 100}
    };

    const getSvgDefs = (zoom) => {
        const items = getMapLayerFadeoutDefs(zoom, colors.colorDark, props.headerHeight);
        return <defs>{items}</defs>;
    };

    return <div className="zol-map" ref={measuredRef}
            onClick={() => {dispatch({type: 'selected', value: null});}}>
        <GeoMap layers={mapLayers}
                htmlLayers={props.isMobile ? [] : htmlLayers}
                zoom={zoomSettings}
                height={props.isMobile ? 600 : props.headerHeight + 1100}
                getSvgDefs={getSvgDefs}
                onHover={setHover}
                Projection={GeoMapProjectionMiller}/>

        {activeItem?.countries ?
            null :
            <div className="cpa-map-legend"
                    style={{bottom: 0, left: zoomSettings.margin.left + 50}}>
                <h2>Legend - Status of ACDEG adoption</h2>
                <ul>
                    <li>
                        <span className="legend-box"
                                style={{backgroundColor: getAcdegColor(CONFIG.acdegStatus.NOT, 2, 0.1)}}/>
                        Not signed
                    </li>
                    <li>
                        <span className="legend-box"
                                style={{backgroundColor: getAcdegColor(CONFIG.acdegStatus.SIGNED, 2, 0.1)}}/>
                        Signed
                    </li>
                    <li>
                        <span className="legend-box" style={{backgroundColor:
                                getAcdegColor(CONFIG.acdegStatus.RATIFIED, 2, 0.1)}}/>
                        Ratified
                    </li>
                </ul>
            </div>
        }

    </div>;
};

Map.propTypes = {
    onHeightChange: PropTypes.func,
    setModalPosition: PropTypes.func,
    modalPosition: PropTypes.arrayOf(PropTypes.number),
    headerHeight: PropTypes.number,
    isMobile: PropTypes.bool
};
Map.defaultProps = {
    onHeightChange: () => {},
    setModalPosition: () => {},
    modalPosition: [0, 0],
    headerHeight: 200,
    isMobile: false
};

export {Map};
