
const rad = (deg) => deg * Math.PI / 180;
const sin = (deg) => Math.sin(rad(deg));
const cos = (deg) => Math.cos(rad(deg));
const tan = (deg) => Math.tan(rad(deg));
const deg = (rad) => rad * 180 / Math.PI;

const defaultOptions = {
    meridian: 0,
    standardParallel: 0,
    latLimit: 85,
    // if we draw shapes in a small area and then blow it up some browser may have trouble with that. For
    // example Firefox will then give inaccurate results for mouse over events. Scalling up the coordinates
    // so we do not have to scale as much using transform will prevent this.
    coordinateScale: 1000
};

const addMeridian = function(coordinate, meridian){
    if(meridian === 0){
        return coordinate;
    }
    return [
        ((coordinate[0] + 180 + 360 - meridian) % 360) - 180,
        coordinate[1]
    ];
};

export {rad, sin, cos, tan, deg, defaultOptions, addMeridian};
