import React, {useEffect} from 'react';
import {PropTypes} from 'prop-types';
import {diamondColorMap} from '../item-list/diamond.js';

const LocationMarker = function(props){
    const colors = Object.values(diamondColorMap);
    const cssColor = colors[(props.itemIndex - 1) % colors.length];
    const position = props.zoom.getPointFromGeolocation(props.coordinates);
    const translate = position.map((p) => (p - 25)).slice(0, 2).join(' ');
    useEffect(() => {
        if(props.modalPosition[0] !== position[0] || props.modalPosition[1] !== position[1]){
            props.setModalPosition(position);
        }
    });

    if(! props.zoom.isReady){
        return null;
    }

    return <g transform={'translate(' + translate + ')'}>
        <g transform="translate(26.05 1.934) rotate(47)" fill="none" stroke="#fff" strokeLinejoin="round"
                strokeWidth="8" opacity="0.7">
            <rect width="33.047" height="33.047" stroke="none"/>
            <rect x="4" y="4" width="25.047" height="25.047" fill="none"/>
        </g>
        <rect width="11" height="11" transform="matrix(0.695, 0.719, -0.719, 0.695, 25.636, 17.333)"
                fill={cssColor}/>
    </g>;
};

LocationMarker.propTypes = {
    zoom: PropTypes.object.isRequired,
    coordinates: PropTypes.arrayOf(PropTypes.number).isRequired,
    itemIndex: PropTypes.number,
    modalPosition: PropTypes.arrayOf(PropTypes.number).isRequired,
    setModalPosition: PropTypes.func.isRequired
};

LocationMarker.defaultProps = {
    color: 1
};

export {LocationMarker};
