import React from 'react';
import {PropTypes} from 'prop-types';
import {UIInputSelect} from './ui/input/select.js';
import {useStateContext} from '../helpers/state-context.js';
import {useData} from '../model/use_data.js';

const SelectItems = function(props){
    const [{selected}, dispatch] = useStateContext(['selected']);
    const {getItem} = useData();
    const options = getItem(props.type)?.reduce((obj, item, index) => {
        obj[index + 1] = item['title-short'];
        return obj;
    }, {}) || {};
    const selectedItem = selected?.type === props.type ? (selected?.index || undefined) : undefined;
    const setValue = (value) => {
        dispatch({type: 'selected', value: {type: props.type, index: value}});
    };

    const placeholder = 'Select ' +
            (props.type === 'unions' ? 'AU institution' : 'Regional Economic Community (RECs)');

    return <div className={'zol-cpa-select zol-cpa-select-' + props.type}>
        <UIInputSelect name={props.type} options={options} value={selectedItem} onChange={setValue}
                placeholder={placeholder}/>
    </div>;
};

SelectItems.propTypes = {
    type: PropTypes.oneOf(['unions', 'recs'])
};
SelectItems.defaultProps = {};

export {SelectItems};
