import React from 'react';
import PropTypes from 'prop-types';
import {useModel} from '../../../helpers/model/use_model.js';
import {GeoMapModelCountry} from '../../../helpers/geo-map/model/country.js';

function MapLayerIslandMarker(props){
    const countryHover = props.hover['country_iso3'] || false;
    const resultSet = useModel(GeoMapModelCountry);
    if(resultSet.status !== GeoMapModelCountry.Status.SUCCESS){
        if(resultSet.status === GeoMapModelCountry.Status.FAILED){
            console.error('Failed to load map shape data. ' + resultSet.error);
        }
        return null;
    }
    const countryDetails = resultSet.data || {};
    const countriesByIso3 = countryDetails.countries || {};
    const result = [];
    for(const iso3 of Object.keys(countriesByIso3)){
        if(! props.includedCountries.includes(iso3)){
            continue;
        }
        const country = countriesByIso3[iso3];
        // 0: no hover
        // 1: hover on this country
        // 2: hover on another country
        // We disable the hover state when the user is panning the map
        const hoverState = props.zoom.isActiveInteraction || countryHover === false ?
                0 : (countryHover === iso3 ? 1 : 2);
        const coords = props.zoom.getPointFromGeolocation(country.centroid);
        const countryProps = {
            style: props.getStyle(country.id, hoverState, props.zoom.scale),
            cx: coords[0],
            cy: coords[1],
            r: props.radius,
            className: 'zol-island-marker' + (hoverState === 1 ? ' zol-island-marker-active' : ''),
            onMouseOver: () => {props.setHover('country_iso3', props.enableHover ? iso3 : false);},
            onMouseOut: () => {
                if(props.enableHover){
                    props.setHover('country_iso3', false);
                }
            },
            onClick: () => {
                if(props.zoom.validateClick()){
                    props.onClick(iso3);
                    props.setHover('country_iso3', false);
                }
            }
        };

        result.push(<circle key={iso3} {...countryProps}/>);
    }

    return <g>
        {result.map((item) => (item))}
    </g>;
}

MapLayerIslandMarker.propTypes = {
    zoom: PropTypes.object.isRequired,
    Projection: PropTypes.func.isRequired,
    getStyle: PropTypes.func,
    hover: PropTypes.object,
    setHover: PropTypes.func,
    onClick: PropTypes.func,
    enableHover: PropTypes.bool,
    includedCountries: PropTypes.array,
    radius: PropTypes.number
};

MapLayerIslandMarker.defaultProps = {
    /* Prevent using opacity/transparency on the map as this has a negative impact on performance */
    getStyle: (iso3, hoverState, zoomScale) => {},
    onHover: (iso3, isActive) => {},
    onClick: (iso3) => {},
    hover: {},
    setHover: (dimension, value) => {},
    enableHover: false,
    includedCountries: ['COM', 'SYC', 'CPV', 'STP', 'MUS'],
    radius: 25
};

export {MapLayerIslandMarker};
