import React, {useState, useEffect} from 'react';
import {PropTypes} from 'prop-types';
import {useData} from '../../model/use_data.js';
import {ReactComponent as IconChevronLeft} from '../../assets/icons/chevron-left-outline.svg';
import {ReactComponent as IconChevronRight} from '../../assets/icons/chevron-right-outline.svg';
import {MarkdownText} from '../../helpers/elements/markdown-text.js';
import './timeline.scss';

const Timeline = function(props){
    const {getItem, isDataReady} = useData();
    const [selectedItem, setSelectedItem] = useState(null);
    useEffect(() => {
        if(! selectedItem && isDataReady){
            setSelectedItem(parseInt(Object.keys(getItem('timeline'))[0], 10));
        }
    }, [isDataReady, getItem, selectedItem, selectedItem]);

    if(! isDataReady){
        return null;
    }
    const timeline = getItem('timeline');
    const years = Object.keys(timeline).map((y) => (parseInt(y, 10)));
    const start = Math.min(...years);
    const end = Math.max(...years);
    const items = [];
    for(let year = start; year <= end; year++){
        items.push({
            year,
            event: timeline[year] || null,
            className: (timeline[year] ? 'zol-enabled' : 'zol-disabled') +
                    (year === selectedItem ? ' zol-selected' : ''),
            isSelected: year === selectedItem
        });
    }
    const width = 100 / (end - start + 1);

    const navigate = (event, direction) => {
        event.stopPropagation();
        const curIndex = years.indexOf(selectedItem);
        if(curIndex >= 0 && years[curIndex + direction]){
            setSelectedItem(years[curIndex + direction]);
        }
    };

    const spacing = props.isMobile ? 2 : 10;

    return <div className="timeline">
        <h2>Timeline</h2>
        <ol>
            {items.map((item) => (
                <li key={item.year} style={{width: 'calc(' + width + '% - ' + spacing + 'px)'}}
                        className={item.className}
                        onClick={item.event ? () => {setSelectedItem(item.year);} : null}>
                    <div>
                        <span className="timeline-year">{item.year}</span>
                        {item.isSelected ?
                            <span className="timeline-event">
                                {props.isMobile ? <h3>{item.year}</h3> : null}
                                <MarkdownText text={item.event}/>
                            </span> :
                            null
                        }
                        {item.isSelected ?
                            <div className="timeline-navigation">
                                <span className={'previous ' +
                                        (item.year === start ? 'zol-disabled' : 'zol-enabled')}
                                        onClick={(e) => {navigate(e, -1);}}>
                                    <IconChevronLeft/>
                                </span>
                                <span className={'next ' +
                                        (item.year === end ? 'zol-disabled' : 'zol-enabled')}
                                        onClick={(e) => {navigate(e, 1);}}>
                                    <IconChevronRight/>
                                </span>
                            </div> :
                            null
                        }
                    </div>
                </li>
            ))}
        </ol>
    </div>;
};

Timeline.propTypes = {
    isMobile: PropTypes.bool
};

Timeline.defaultProps = {
    isMobile: false
};

export {Timeline};
