import React from 'react';
import {ModelCountryDetails} from './country-details.js';
import {useModel} from '../helpers/model/use_model.js';

const useCountryDetails = () => {
    const [data, setData] = React.useState({list: [], countries: {}});
    const resultSet = useModel(ModelCountryDetails);

    React.useEffect(() => {
        if(resultSet.status === ModelCountryDetails.Status.SUCCESS){
            setData(resultSet.data || {});
        }else if(resultSet.status === ModelCountryDetails.Status.FAILED){
            console.error('Failed to load data: ' + resultSet.error);
        }
    }, [resultSet]);

    const getCountryDetails = (iso3) => (data.countries[iso3] || null);
    const isDataReady = Object.keys(data.list).length > 0;

    return {getCountryDetails, data, isDataReady};
};

export {useCountryDetails};
