import {SimpleEvent} from '../simple-event.js';

class PostMessage {
    constructor(settings){
        this.settings = {
            originUri: settings.originUri || document.location.origin,
            targetUri: settings.targetUri || document.location.origin,
            callback: settings.callback || function(){},
            otherWindow: settings.otherWindow || null,
            iframe: settings.iframe || null,
            sendInit: ! (settings.sendInit === false)
        };

        this.initiated = false;
        this.sendStack = [];

        this.event = new SimpleEvent(window, 'message', [this, this.receive]);

        if(this.settings.sendInit){
            this.initiated = true;
            this.send({type: 'init'});
        }
    }

    receive(event){
        if(event.origin !== this.settings.targetUri &&
                (this.settings.otherWindow === null || event.source !== this.settings.otherWindow)){
            if(process.env.NODE_ENV === 'development'){
                console.warn('received message ignored, origins don\'t match ' +
                        event.origin + ' and ' + this.settings.targetUri +
                        ' and message originates from unknown source window.');
            }
            return;
        }

        if(event.origin === this.settings.targetUri && this.settings.otherWindow === null){
            this.settings.otherWindow = event.source;
        }

        if(! event.data || ! event.data.type){
            return;
        }

        if(event.data.type === 'init'){
            this.initiated = true;
        }

        this.settings.callback(event.data);

        if(this.initiated){
            for(const payload of this.sendStack){
                this.send(payload);
            }
            this.sendStack = [];
        }
    }

    send(data){
        if(! this.initiated){
            this.sendStack.push(data);
            return;
        }
        if(this.settings.otherWindow === null){
            this.settings.otherWindow = window.parent;
        }
        try {
            // although normally we provide the contentWindow of the iframe in the otherWindow property, in
            // some occasions the host page touches the content and the contentWindow is no longer valid for
            // some reason. Passing the iframe element and getting the contentWindow directly from the element
            // solves the issue.
            if(this.settings.iframe){
                this.settings.iframe.contentWindow.postMessage(data, this.settings.targetUri);
            }else{
                this.settings.otherWindow.postMessage(data, this.settings.targetUri);
            }
        } catch (e){
            console.log('communication failed to ' + this.settings.targetUri);
        }
    }

    remove(){
        if(this.event){
            this.event.remove();
        }

        this.settings = null;
    }
}

export {PostMessage};
