import React, {useEffect, useState} from 'react';
import {stringify} from '../functions/stringify.js';
import {Transformation} from '../math/transformation.js';
import {getTransformationCallback} from './get_transformation_callback.js';
import {InteractionManager} from './interaction-manager.js';
import {MathProjectionAsSource} from '../math/projection/as-source.js';
import {getProjectedBounds} from './get_projected_bounds.js';
import {ZoomControls} from './controls.js';

const defaultSettings = {
    enabled: true,
    enablePanning: true,
    enableZoom: true,
    enableScrollWheel: true,
    enableControls: true,
    bounds: [[0, 0], [1, 1]], // The area of the source image where we want to stay in
    // if we do not want to use the whole svg, but only a part, we can define the margins here. Note that
    // cutting off the content outside of the margin remains the responsibility of the layers using this zoom
    margin: {top: 0, right: 0, bottom: 0, left: 0},
    // Available zoom range. Zoom level 1 is the full bounds, in other words: show all. We can't go below 1.
    zoomRange: [1, 7],
    level: 1, // The zoom level to start in
    center: [0, 0], // The center coordinates (of the source image) to start in
    onZoom: () => {},
    onPan: () => {},
    // Size of the view
    width: 1,
    height: 1,
    getElementScreenPosition: () => ([0, 0]),
    // Projection class to use on the source image
    Projection: MathProjectionAsSource,
    animationDuration: 300
};

const useZoom = function(settings, element){
    settings = {...defaultSettings, ...settings, scale: 1};
    const zoom = {
        isReady: false,
        level: 1,
        scale: 1,
        center: [0, 0],
        margin: settings.margin,
        transformation: new Transformation(),
        width: Math.max(0, settings.width - settings.margin.left - settings.margin.right),
        height: Math.max(0, settings.height - settings.margin.top - settings.margin.bottom),
        validateClick: () => (true)
    };

    const {Projection, getElementScreenPosition} = settings;
    const {width, height, margin} = zoom;

    // If any of these keys in the settings change we need to reset the zoom
    const watchKeys = ['enabled', 'zoomRange', 'bounds', 'level', 'center', 'margin'];
    const watchSettings = watchKeys.reduce((obj, key) => {obj[key] = settings[key]; return obj;}, {});
    const hash = stringify(watchSettings);

    const [zoomSettings, setZoomSettings] = useState(
            {...watchSettings, Projection, width, height, margin, getElementScreenPosition});
    const [currentHash, setCurrentHash] = useState(hash);

    // because the projection might not be linear, we can't assume the corners of the mapBounds are
    // representing the outer edges of our final image. We get a few points around the edge to get the bound
    // for the resulting image
    const imageBounds = getProjectedBounds(zoomSettings.bounds, Projection);

    // Scaling the map appropriate to the size of the svg
    const scaleX = Math.max(width, 1) / (imageBounds[1][0] - imageBounds[0][0]);
    const scaleY = Math.max(height, 1) / (imageBounds[1][1] - imageBounds[0][1]);
    // The scale calculated here corresponds to zoom level 1
    const scale = Math.abs(scaleX) < Math.abs(scaleY) ? scaleX : scaleY;
    const zoomScale = scale * zoomSettings.level;

    // reset the zoom
    useEffect(() => {
        if(hash !== currentHash){
            setCurrentHash(hash);
            setZoomSettings((zoomSettings) => {return {...zoomSettings, ...watchSettings};});
        }
    // Only update when the hash changes
    // eslint-disable-next-line
    }, [hash, currentHash]);

    // calculate new zoom (based on same zoom parameters)
    useEffect(() => {
        setZoomSettings((zoomSettings) => {
            return {...zoomSettings, width, height, Projection, scale: zoomScale};
        });
    }, [Projection, width, height, zoomScale]);

    const applyTransformation = (delta, animationDuration, cursorScreenLocation = false) => {
        // We cannot directly access the zoomSettings, because this function is connected to a event handler
        // once the svg element is available. This reference is not updated afterwards. Using a callback
        // function in setZoomSettings to by-pass this limitation
        setZoomSettings(getTransformationCallback(delta, animationDuration, cursorScreenLocation));
    };

    const [interactionManager, setInteractionManager] = useState(null);
    useEffect(() => {
        if(element !== null && zoomSettings.enabled === true){
            // setting up the event listeners using Hammerjs for touch gestures
            const interactionSettings = {
                enableScrollWheel: settings.enableScrollWheel,
                animationDuration: settings.animationDuration
            };
            const im = new InteractionManager(element, applyTransformation, interactionSettings);
            setInteractionManager(im);
            setZoomSettings((currentZoom) => {
                return {
                    ...currentZoom,
                    getElementScreenPosition: () => {
                        const box = element.getBoundingClientRect();
                        return [currentZoom.margin?.left + box.left, currentZoom.margin.top + box.top];
                    }
                };
            });
        }
        // clean up the hammerManager when the map component is removed or when the element is about to change
        return () => {
            if(interactionManager){
                interactionManager.remove();
            }
        };
    // Dependency interactionManager is left out on purpose
    // eslint-disable-next-line
    }, [element, settings.enableScrollWheel, settings.animationDuration, zoomSettings.enabled]);

    if(element === null){
        return zoom;
    }

    // Make sure the image is in the center of the view
    const translate = [
        settings.margin.left / zoomScale + 0.5 * width / zoomScale,
        settings.margin.top / zoomScale + 0.5 * height / zoomScale
    ];
    const centerProjected = Projection.apply(zoomSettings.center);

    zoom.isReady = true;
    zoom.level = zoomSettings.level;
    zoom.scale = zoomScale;
    zoom.center = zoomSettings.center;
    zoom.transformation.setPosition(
            translate[0] - centerProjected[0],
            translate[1] - centerProjected[1]);
    zoom.transformation.setScale(zoomScale);

    // Is true when user is dragging/pinching the map
    zoom.isActiveInteraction = interactionManager ? interactionManager.isActive() : false;

    // Call this in a click event handler to see if the click was not part of a gesture (pan) and should be
    // ignored
    zoom.validateClick = interactionManager ?
            interactionManager.validateClick.bind(interactionManager) : () => (true);

    // Helper functions to translate locations on the screen to geo locations on the map and visa versa
    zoom.getPointFromGeolocation = (coordinates) => {
        const location = Projection.apply(coordinates);
        const matrix = zoom.transformation.getTransformationMatrix();
        return matrix.multiplyVector(location);
    };
    zoom.getGeoLocationFromPoint = (point) => {
        const transformation = new Transformation();
        transformation.setObjectScale(1 / zoomScale);
        transformation.setPosition(
                -1 * (translate[0] - centerProjected[0]),
                -1 * (translate[1] - centerProjected[1]));
        const location = transformation.getTransformationMatrix().multiplyVector(point);
        return Projection.inverse(location);
    };
    zoom.getPointFromScreenLocation = (point) => {
        const mapPosition = zoomSettings.getElementScreenPosition();
        return [
            point[0] - mapPosition[0],
            point[1] - mapPosition[1]
        ];
    };

    zoom.controls = settings.enableControls ?
            <ZoomControls zoomSettings={zoomSettings} setZoomSettings={setZoomSettings}/> :
            null;

    return zoom;
};

export {useZoom};
