import React from 'react';
import PropTypes from 'prop-types';
import {useModel} from '../../../../helpers/model/use_model.js';
import {GeoMapModelTopoJson} from '../../model/topo-json.js';

function GeoMapLayerCountries(props){
    const countryHover = props.hover['country_iso3'] || false;
    const resultSet = useModel(props.modelShapes, {Projection: props.Projection}, props.modelShapesFilter);
    if(resultSet.status !== GeoMapModelTopoJson.Status.SUCCESS){
        if(resultSet.status === GeoMapModelTopoJson.Status.FAILED){
            console.error('Failed to load map shape data. ' + resultSet.error);
        }
        return null;
    }
    const countryPaths = resultSet.data || [];
    const result = [];
    for(const country of countryPaths){
        // 0: no hover
        // 1: hover on this country
        // 2: hover on another country
        // We disable the hover state when the user is panning the map
        const hoverState = props.zoom.isActiveInteraction || countryHover === false ?
                0 : (countryHover === country.id ? 1 : 2);
        const countryProps = {
            style: props.getStyle(country.id, hoverState, props.zoom.scale),
            d: country.path,
            className: 'zol-country' + (hoverState === 1 ? ' zol-country-active' : ''),
            onMouseOver: () => {props.setHover('country_iso3', props.enableHover ? country.id : false);},
            onMouseOut: () => {
                if(props.enableHover){
                    props.setHover('country_iso3', false);
                }
            },
            onClick: () => {
                if(props.zoom.validateClick()){
                    props.onClick(country.id);
                    props.setHover('country_iso3', false);
                }
            }
        };

        result.push(<path key={country.id} {...countryProps}/>);
    }

    return (
        <g transform={props.zoom.transformation.getTransformationString()}>
            {result.map((item) => (item))}
        </g>
    );
}

GeoMapLayerCountries.propTypes = {
    zoom: PropTypes.object.isRequired,
    Projection: PropTypes.func.isRequired,
    getStyle: PropTypes.func,
    hover: PropTypes.object,
    setHover: PropTypes.func,
    onClick: PropTypes.func,
    modelShapes: PropTypes.func,
    modelShapesFilter: PropTypes.func,
    enableHover: PropTypes.bool
};

GeoMapLayerCountries.defaultProps = {
    /* Prevent using opacity/transparency on the map as this has a negative impact on performance */
    getStyle: (iso3, hoverState, zoomScale) => {},
    onHover: (iso3, isActive) => {},
    onClick: (iso3) => {},
    hover: {},
    setHover: (dimension, value) => {},
    modelShapes: GeoMapModelTopoJson,
    modelShapesFilter: (m) => (m.getData()),
    enableHover: false
};

export {GeoMapLayerCountries};
