const createPath = function(coords, Projection){
    let data = '';

    for(let i = 0; i < coords.length; i++){
        const v = coords[i];

        if(v[0].length === 2 && v[0][0] === +v[0][0]){
            let last = v[v.length - 1][0];
            data += 'M';
            for(let c = 0, l = v.length; c < l; c++){
                const vo = v[c].slice(0);
                if(last && (Math.max(vo[0], last) - Math.min(vo[0], last)) > 180){
                    if(last < 0){
                        vo[0] = (vo[0] < 180) ? -180 : vo[0] - 360;
                    }else{
                        vo[0] = (vo[0] > -180) ? 180 : vo[0] + 360;
                    }
                }
                last = vo[0];
                const vn = Projection.apply(vo);

                data += vn[0] + ' ' + vn[1];
                if(c !== l - 1 && vn.length > 2){
                    data += ' ' + vn[2];
                }else if(c !== l - 1){
                    data += ' L';
                }else{
                    data += ' Z';
                }
            }
        }else{
            const d = createPath(v, Projection);
            data += ' ' + d;
        }
    }

    return data;
};

export {createPath};
