import React from 'react';
import {PropTypes} from 'prop-types';
import colors from '../../assets/variables.module.scss';

const ItemListDiamondMobile = function(props){
    const cssColor = props.type === 'recs' ? colors.colorGreen : colors.colorPink;

    return <svg width="22" height="22" viewBox="0 0 22 22" className="zol-cpa-diamond-mobile">
        <g transform="translate(10.607) rotate(45)" fill="none" stroke={cssColor} strokeWidth="3">
            <rect width="15" height="15" rx="4" stroke="none"/>
            <rect x="1.5" y="1.5" width="12" height="12" rx="2.5" fill="none"/>
        </g>
    </svg>;
};

ItemListDiamondMobile.propTypes = {
    type: PropTypes.oneOf(['recs', 'unions'])
};

ItemListDiamondMobile.defaultProps = {
    type: 'unions'
};

export {ItemListDiamondMobile};
