import {PostMessage} from './post-message.js';
import {SimpleEvent} from '../simple-event.js';
import {getElementPagePosition} from '../functions/get_element_page_position.js';
import {scrollTo, scrollY} from '../scroll_to.js';
import {CONFIG} from '../../config.js';

const defaultOptions = {
    parentLocation: window.location.href.split('/').slice(0, 3).join('/'),
    enableShadowDom: true,
    defaultHeight: 300
};

class EmbeddedApp {
    constructor(parentElement, iframeUrl = null, options = {}){
        this.options = {...defaultOptions, ...options};
        this.container = document.createElement('div');

        if(parentElement.childNodes.length === 0){
            parentElement.appendChild(this.container);
        }else{
            parentElement.insertBefore(this.container, parentElement.childNodes[0]);
        }

        this.container.style.width = '100%';

        this.objects = [];
        this.shouldInitialize = true;
        this.iframeElm = null;
        this.communication = null;
        this.timerId = null;
        this.iframeUrl = iframeUrl;
        this.onRemove = () => {};
        this.onReceive = options.onReceive || (() => (true));

        this.containerShadow = this.container;
        if(this.container.attachShadow && options.enableShadowDom){
            this.containerShadow = this.container.attachShadow({mode: 'open'});
        }else if(iframeUrl){
            // We are not in the iframe, so we are in the parent. Create the iframe.
            this.shouldInitialize = false;
            this.setupIframe();
        }else{
            // setup communication with parent frame
            this.communication = new PostMessage({
                originUri: document.location.origin,
                targetUri: '*',
                otherWindow: null,
                sendInit: true,
                callback: this.onIframeReceiveMessage.bind(this)
            });
            this.objects.push(this.communication);
        }
    }

    onIframeReceiveMessage(data){
        if(! data){
            return;
        }
        if(this.onReceive(data) === false){
            return;
        }
        // receive the settings, init the visualization
        if(data['type'] === 'settings'){
            let curHeight = 300;
            this.options.parentLocation = data.data.parentLocation || this.options.parentLocation;
            this.setIframeConfig(data.data.config);
            this.timerId = setInterval(() => {
                function getDocHeight(doc){
                    const body = doc.body;
                    const html = doc.documentElement;
                    const height = Math.max(body.scrollHeight, body.offsetHeight,
                            html.clientHeight, html.scrollHeight, html.offsetHeight);
                    return height;
                }
                const h = getDocHeight(document);
                if(h !== curHeight){
                    this.communication.send({type: 'height', data: h});
                    curHeight = h;
                }
            }, 500);
            this.communication.send({type: 'config-set'});
        }else if(data.type === 'scrollTop'){
            window.iFramesScrollTop = data.value;
        }
    }

    setIframeConfig(config){
        if(config && typeof config === 'object'){
            for(const key of Object.keys(config)){
                if(CONFIG[key] && typeof CONFIG[key] === 'object'){
                    // insert config properties into the existing config object
                    CONFIG[key] = {...CONFIG[key], ...config[key]};
                }else{
                    // set new value for config value
                    CONFIG[key] = config[key];
                }
            }
        }
    }

    onParentReceiveMessage(data){
        if(! data){
            return;
        }
        if(this.onReceive(data) === false){
            return;
        }
        if(data['type'] === 'height'){
            this.iframeElm.setAttribute('height', data.data);
            this.sendScrollTop();
        }

        if(data['type'] === 'init'){
            this.communication.send({
                type: 'settings',
                data: {
                    s: 1,
                    parentLocation: this.options.parentLocation,
                    config: this.options.config || {}
                }
            });
        }

        if(data['type'] === 'config-set'){
            this.communication.send({type: 'start'});
        }

        if(data.type === 'scrollTop'){
            const offset = this.iframeElm.getBoundingClientRect().top + scrollY();
            scrollTo(null, data.value + offset);
        }
    }

    setupIframe(){
        const uri = this.iframeUrl;
        const pathArray = uri.split('/');
        const protocol = pathArray[0];
        const host = pathArray[2];
        const targetUri = protocol + '//' + host;

        // create the iframe
        const iframe = document.createElement('iframe');
        iframe.setAttribute('width', '100%');
        iframe.setAttribute('height', this.options.defaultHeight + 'px');
        iframe.setAttribute('frameborder', '0');
        iframe.setAttribute('scrolling', 'no');
        iframe.setAttribute('src', uri);

        // add it to container or parent node of script
        this.container.appendChild(iframe);

        this.iframeElm = iframe;

        // setup communication with iframe
        // normally just sharing the contentWindow of the iframe to PostMessage should be sufficient, however
        // we've had occasions where the refference to contentWindow gets lost. That's why we also share the
        // iframe element itself from where we can get the current contentWindow refference.
        this.communication = new PostMessage({
            originUri: document.location.origin,
            targetUri,
            otherWindow: iframe.contentWindow,
            iframe,
            sendInit: false,
            callback: this.onParentReceiveMessage.bind(this)
        });

        this.objects.push(this.communication);

        const scrollEvent = new SimpleEvent(window, 'scroll', [this, this.sendScrollTop]);
        this.objects.push(scrollEvent);
    }

    sendScrollTop(){
        const body = document.body;
        const docEl = document.documentElement;
        const scrollTop = window.pageYOffset || docEl.scrollTop || body.scrollTop;

        this.communication.send({
            type: 'scrollTop',
            value: scrollTop - getElementPagePosition(this.iframeElm).top
        });
    }

    init(onInit){
        if(this.shouldInitialize){
            this.onRemove = onInit(this.containerShadow) || (() => {});
        }
    }

    remove(){
        clearInterval(this.timerId);

        for(const obj of this.objects){
            if(!!obj && !!obj.remove){
                obj.remove();
            }
        }

        this.objects = [];
        this.onRemove();
        this.container.parentNode.removeChild(this.container);
        this.container = null;
        this.iframeElm = null;
    }
}

export {EmbeddedApp};
