import React, {useEffect} from 'react';
import {PropTypes} from 'prop-types';
import {useStateContext} from '../../helpers/state-context.js';
import {useData} from '../../model/use_data.js';
import {useSize} from '../../helpers/use_size.js';
import {DetailsBackground} from './background.js';
import {diamondColorMap} from '../item-list/diamond.js';
import {MarkdownText} from '../../helpers/elements/markdown-text.js';

function Details(props){
    const [{selected}, dispatch] = useStateContext(['selected']);
    const {getItem} = useData();
    const [size, measuredRef, containerElement] = useSize((w, h) => {return {width: w || 0, height: h || 0};},
            {width: 0, height: 0});

    const bottomPosition = props.position[1] + size.height;
    useEffect(() => {
        props.setBottonPosition(bottomPosition);
    }, [bottomPosition]);

    useEffect(() => {
        if(containerElement !== null && selected !== null){
            setTimeout(() => {
                const box = containerElement.getBoundingClientRect();
                window.scrollTo(0, box.top - 200);
            }, 200);
        }
    }, [selected, containerElement]);

    if(selected === null){
        return null;
    }
    const colors = Object.keys(diamondColorMap);
    const activeColor = colors[(selected.index - 1) % colors.length];

    const items = getItem(selected.type);
    const item = items ? items[selected.index - 1] || null : null;
    const onClose = () => {dispatch({type: 'selected', value: null});};
    if(item === null){
        setTimeout(() => {onClose();}, 0);
    }

    const left = props.isMobile ? 10 : 0.5 * (window.innerWidth - size.width);

    return <div className={'zol-cpa-details'} style={{top: props.position[1], left}}>
        <DetailsBackground size={size} color={activeColor} arrowLeft={props.position[0] - left}/>
        <div className={'zol-cpa-details-content'} ref={measuredRef}>
            <span className="zol-cpa-details-close" onClick={onClose}>Close</span>
            <h2>{item.title}</h2>
            <MarkdownText text={item.description}/>
            <a href={item.url} className={'zol-cpa-button zol-cpa-button-' + activeColor} target="_blank"
                    rel="noreferrer">
                Continue reading
            </a>
        </div>
    </div>;
};

Details.propTypes = {
    position: PropTypes.arrayOf(PropTypes.number).isRequired,
    setBottonPosition: PropTypes.func.isRequired,
    isMobile: PropTypes.bool
};

Details.defaultProps = {
    isMobile: false
};

export {Details};
