import React, {useState, useEffect} from 'react';
import {PropTypes} from 'prop-types';
import {useData} from '../../model/use_data.js';
import {useStateContext} from '../../helpers/state-context.js';
import {useSize} from '../../helpers/use_size.js';
import {SelectItems} from '../../components/select-items.js';
import {MarkdownText} from '../../helpers/elements/markdown-text.js';

const ViewHeader = function(props){
    const {getItem, isDataReady} = useData();
    const [isShowAllText, setIsShowAllText] = useState(false);
    const [{selected}] = useStateContext(['selected']);
    const showNote = selected === null;
    const [mapHeight, measuredRef] = useSize((w, h) => (h || 200), 200);
    useEffect(() => {
        props.onHeightChange(mapHeight); // adding padding
    }, [mapHeight]);

    const allText = (isDataReady ? getItem('introduction') : '').split('\n');
    const firstText = allText[0];
    const moreText = allText.length > 1 ? allText.slice(1).join('\n') : '';

    return <div className="zol-cpa-header" ref={measuredRef}>
        <h1>{getItem('title')}</h1>
        <MarkdownText text={firstText}/>
        {isShowAllText ?
            <div className="zol-cpa-intro-all">
                <MarkdownText text={moreText}/>
            </div> :
            null
        }
        <span className="zol-cpa-show-intro" onClick={() => {setIsShowAllText(! isShowAllText);}}>
            {isShowAllText ? 'Hide text' : 'Show more'}
        </span>

        <p className="zol-cpa-select-note">
            {showNote ? getItem('select-organization') : <>&nbsp;</>}
        </p>
        {props.isMobile ? <SelectItems type="unions"/> : null}
        {props.isMobile ? <SelectItems type="recs"/> : null}
    </div>;
};

ViewHeader.propTypes = {
    onHeightChange: PropTypes.func,
    isMobile: PropTypes.bool
};
ViewHeader.defaultProps = {
    onHeightChange: () => {},
    isMobile: false
};

export {ViewHeader};
