import React from 'react';
import PropTypes from 'prop-types';

function MapLayerFadeout(props){
    const width = props.zoom.width + props.zoom.margin.left + props.zoom.margin.right;
    const height = props.zoom.height + props.zoom.margin.top + props.zoom.margin.bottom;
    const fadeWidth = typeof props.fadeWidth === 'object' ?
        props.fadeWidth :
        {top: props.fadeWidth, bottom: props.fadeWidth, left: props.fadeWidth, right: props.fadeWidth};
    return (
        <g className={props.className}>
            {/* Top fade */}
            {props.directions.top ?
                <rect x={0}
                        y={0}
                        width={width}
                        height={fadeWidth.top}
                        style={{fill: 'url(#mapLayerFadeoutTop)'}}/> :
                        null
            }
            {/* Right fade */}
            {props.directions.right ?
                <rect x={width - fadeWidth.right}
                        y={0}
                        width={fadeWidth.right}
                        height={height}
                        style={{fill: 'url(#mapLayerFadeoutRight)'}}/> :
                        null
            }
            {/* Bottom fade */}
            {props.directions.bottom ?
                <rect x={0}
                        y={height - fadeWidth.bottom}
                        width={width}
                        height={fadeWidth.bottom}
                        style={{fill: 'url(#mapLayerFadeoutBottom)'}}/> :
                        null
            }
            {/* Left fade */}
            {props.directions.left ?
                <rect x={0}
                        y={0}
                        width={fadeWidth.left}
                        height={height}
                        style={{fill: 'url(#mapLayerFadeoutLeft)'}}/> :
                null
            }
        </g>
    );
}

MapLayerFadeout.propTypes = {
    zoom: PropTypes.object.isRequired,
    Projection: PropTypes.func.isRequired,
    className: PropTypes.string,
    fadeWidth: PropTypes.oneOfType([PropTypes.number, PropTypes.object]),
    directions: PropTypes.shape({
        top: PropTypes.bool,
        bottom: PropTypes.bool,
        left: PropTypes.bool,
        right: PropTypes.bool
    })
};

MapLayerFadeout.defaultProps = {
    className: 'zol-cpa-map-layer-fadeout',
    fadeWidth: 150,
    backgroundColor: 'white',
    directions: {
        top: true,
        bottom: true,
        left: true,
        right: true
    }
};

const getMapLayerFadeoutDefs = function(zoom, stopColor, headerHeight){
    const styleOuter = {stopColor, stopOpacity: 1};
    const styleMiddle = {stopColor, stopOpacity: 0.8};
    const styleInner = {stopColor, stopOpacity: 0};
    return [
        <linearGradient key="top" id="mapLayerFadeoutTop" x1="0%" y1="0%" x2="0%" y2="100%">
            <stop offset="0%" style={styleOuter}/>
            <stop offset={headerHeight > 400 ? '80%' : '50%'} style={styleMiddle}/>
            <stop offset="100%" style={styleInner}/>
        </linearGradient>,
        <linearGradient key="right" id="mapLayerFadeoutRight" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" style={styleInner}/>
            <stop offset="100%" style={styleOuter}/>
        </linearGradient>,
        <linearGradient key="bottom" id="mapLayerFadeoutBottom" x1="0%" y1="0%" x2="0%" y2="100%">
            <stop offset="0%" style={styleInner}/>
            <stop offset="100%" style={styleOuter}/>
        </linearGradient>,
        <linearGradient key="left" id="mapLayerFadeoutLeft" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" style={styleOuter}/>
            <stop offset="100%" style={styleInner}/>
        </linearGradient>
    ];
};

export {MapLayerFadeout, getMapLayerFadeoutDefs};
