import {ModelXhr} from '../../model/xhr.js';
import {createPath} from '../function/create_path.js';
import topojsonFeature from '../../vendor/topojson/feature.js';
import {CONFIG} from '../../../config.js';
import {getHash} from '../../functions/get_hash.js';

class GeoMapModelTopoJson extends ModelXhr {
    static modelName = 'GeoMapModelTopoJson';

    static getSelect(input){
        return input;
    }

    constructor(selectData, config = {}){
        super(selectData, {
            endpoint: config.endpoint || (CONFIG.static_root + 'data/zolabo-map.json'),
            method: config.method || 'GET'
        });
        this.name = 'GeoMapModelTopoJson';
        this.featuresName = config.featuresName || 'zolabo-map';

        // this enables caching
        this.cacheEnabled = true;
        this.cacheLimit = 1;
        this.filterHash = null;
        // caching the processed data. This should change when the selectionCriteria of the model change (the
        // projection). There will be a new instance of this model for each unique set of selectionCriteria.
        this.processedData = null;
    }

    getSelect(input){
        return {};
    }

    getMetaFromResponse(data){
        try {
            const json = JSON.parse(data);
            return {
                bbox: json.bbox?.slice() || [-180, -90, 180, 90],
                scale: json.transform?.scale?.slice() || [1, 1],
                translate: json.transform?.translate?.slice() || [0, 0]
            };
        } catch (e){}
        return null;
    }

    getDataFromResponse(data){
        try {
            const json = JSON.parse(data);
            return json;
        } catch (e){
            const error = 'Error loading map data: ' + e.message;
            this.onFailure(error);
        }
        return null;
    }

    getData(getItemDetails = null, filter = []){
        if(this.status === GeoMapModelTopoJson.Status.SUCCESS){
            // check if we have already processed the data with this projection and filter. If so, load the
            // cached result.
            const filterHash = getHash({getItemDetails, filter});
            const result = filterHash === this.filterHash && this.processedData ?
                    this.processedData : this.processCountryPaths(this.data, getItemDetails, filter);
            this.filterHash = filterHash;
            return {
                status: this.status,
                meta: this.meta,
                data: result?.countryPaths || [],
                bounds: result?.bounds,
                error: this.error
            };
        }
        return super.getData();
    }

    // Process the country paths. Call this when Model is in state success and this.data is set
    processCountryPaths(data, getItemDetails, filter){
        if(! this.selectData?.Projection || typeof this.selectData.Projection.apply !== 'function'){
            console.error('No projection provided in selection criteria for GeoMapModelTopoJson');
            return null;
        }

        const countries = topojsonFeature(data, data.objects[this.featuresName]);
        if(countries.type === 'Feature' && ! countries.features){
            countries.features = [{id: this.featureName, geometry: countries.geometry}];
        }

        const countryIndexes = {};
        const countryPaths = [];

        for(let i = 0; i < countries.features.length; i++){
            const c = countries.features[i];

            // feature without geometry or feature we want to filter out. Skip.
            if(c.geometry === null || this.isFeatureInFilter(c, getItemDetails, filter) === false){
                continue;
            }

            c.path = createPath(c.geometry.coordinates, this.selectData.Projection);

            if(countryIndexes[c.id]){
                // Multiple paths for the same country found. Merge them
                countryPaths[countryIndexes[c.id]].path += ' ' + c.path;
            }else{
                countryIndexes[c.id] = countryPaths.length;
                countryPaths.push(c);
            }
        }
        // get the bounding box of the filtered countries
        const bounds = getItemDetails ? this.getCountryBounds(countryPaths, getItemDetails) : null;

        this.processedData = {countryPaths, bounds};
        return {countryPaths, bounds};
    }

    getFilteredFeatures(features, getItemDetails, filter){
        if(! getItemDetails || filter.length === 0){
            return features;
        }
        return features.filter((d) => (
            this.isFeatureInFilter(d, getItemDetails, filter)
        ));
    }

    isFeatureInFilter(feature, getItemDetails, filter){
        if(filter.length === 0){
            return true;
        }
        const countryDetails = getItemDetails ? getItemDetails(feature.id) : null;
        return filter.some((f) => (
            Object.keys(f).every((dim) => (
                dim === 'iso3' && f[dim] === feature.id ||
                        (countryDetails && countryDetails[dim] === f[dim])
            ))
        ));
    }

    getCountryBounds(countries, getItemDetails){
        const getCountryBBox = (iso3) => {
            const country = getItemDetails(iso3);
            return country?.bbox || null;
        };
        return countries.map((d) => (getCountryBBox(d.id)))
                .filter((bbox) => (bbox !== null))
                .reduce((bounds, box) => (
                    [
                        [Math.min(box[0][0], bounds[0][0]), Math.max(box[0][1], bounds[0][1])],
                        [Math.max(box[1][0], bounds[1][0]), Math.min(box[1][1], bounds[1][1])]
                    ]
                ), [[Infinity, -Infinity], [-Infinity, Infinity]]);
    }

    getBounds(){
        let data = [];
        if(this.status === GeoMapModelTopoJson.Status.SUCCESS){
            data = [
                [
                    Math.min(this.meta.bbox[0], this.meta.bbox[2]),
                    Math.max(this.meta.bbox[1], this.meta.bbox[3])
                ],
                [
                    Math.max(this.meta.bbox[0], this.meta.bbox[2]),
                    Math.min(this.meta.bbox[1], this.meta.bbox[3])
                ]
            ];
        }
        return {
            status: this.status,
            data
        };
    }
}

export {GeoMapModelTopoJson};
