import {ModelXhr} from '../helpers/model/xhr.js';
import {CONFIG} from '../config.js';

class ModelData extends ModelXhr {
    static modelName = 'ModelData';

    static getSelect(input){
        return input;
    }

    constructor(selectData, options = {}){
        super(selectData, {
            endpoint: CONFIG.data_file ||
                    (CONFIG.static_root + 'data/data.json') + '?v=' + CONFIG.dataVersion,
            method: options.method || 'GET'
        });

        this.cacheEnabled = true;
    }

    getPayload(select){
        return {};
    }

    getDataFromResponse(data){
        try {
            return JSON.parse(data);
        } catch (e){
            if(this.request && this.request.aborted){
                console.warn('Cannot process request, probably because it was aborted. Exception: ' +
                    e.message);
                this.status = 'waiting';
            }else{
                this.onFailure(e);
            }
        }
        return {};
    }

    getSelect(input){
        return ModelData.getSelect(input);
    }
}

export {ModelData};
