import {ModelXhr} from '../helpers/model/xhr.js';
import {CONFIG} from '../config.js';

class ModelCountryDetails extends ModelXhr {
    static modelName = 'ModelCountryDetails';

    static getSelect(input){
        return input;
    }

    constructor(selectData, options = {}){
        super(selectData, {
            endpoint: CONFIG.data_country_file ||
                    (CONFIG.static_root + 'data/countries.json') + '?v=' + CONFIG.dataVersion,
            method: options.method || 'GET'
        });

        this.cacheEnabled = true;
    }

    getPayload(select){
        return {};
    }

    getDataFromResponse(data){
        try {
            const dataJson = JSON.parse(data);
            const result = {list: dataJson, countries: {}};
            for(const country of dataJson){
                // Note: some countrie have not signed, but ratified the ACDEG. In that case we show it as
                // ratified instead of not signed.
                country.acdeg_status = country.ratification_date !== '-' ?
                        CONFIG.acdegStatus.RATIFIED :
                        (country.signature_date === '-' ?
                            CONFIG.acdegStatus.NOT :
                            CONFIG.acdegStatus.SIGNED);
                const updateDate = country.acdeg_status === CONFIG.acdegStatus.SIGNED ?
                        country.signature_date :
                        (country.acdeg_status === CONFIG.acdegStatus.RATIFIED ?
                            country.ratification_date : null);
                country.acdeg_update_year = updateDate ? updateDate.split('-')[2] : null;
                result.countries[country.iso3] = country;
            }
            return result;
        } catch (e){
            if(this.request && this.request.aborted){
                console.warn('Cannot process request, probably because it was aborted. Exception: ' +
                    e.message);
                this.status = 'waiting';
            }else{
                this.onFailure(e);
            }
        }
        return {};
    }

    getSelect(input){
        return ModelCountryDetails.getSelect(input);
    }
}

export {ModelCountryDetails};
