import React from 'react';
import {PropTypes} from 'prop-types';
import './style.scss';

const ZoomControls = function(props){
    const resetZoom = () => {
        props.setZoomSettings({...props.zoomSettings, level: 1, center: [0, 0]});
    };
    const setZoom = (scale) => {
        const newLevel = Math.min(
                Math.max(props.zoomSettings.level * scale, props.zoomSettings.zoomRange[0]),
                props.zoomSettings.zoomRange[1]);
        props.setZoomSettings({...props.zoomSettings, level: newLevel});
    };
    const zoomInClass = 'zol-zoom-in' +
            (props.zoomSettings.level >= props.zoomSettings.zoomRange[1] ? ' zol-disabled' : '');
    const zoomOutClass = 'zol-zoom-out' +
            (props.zoomSettings.level <= props.zoomSettings.zoomRange[0] ? ' zol-disabled' : '');

    return <div className="zol-zoom-controls">
        <span className={zoomInClass} onClick={() => {setZoom(2);}}>+</span>
        <span className={zoomOutClass} onClick={() => {setZoom(0.5);}}>-</span>
        <span className="zol-zoom-reset" onClick={resetZoom}>x</span>
    </div>;
};

ZoomControls.propTypes = {
    zoomSettings: PropTypes.shape({
        level: PropTypes.number.isRequired,
        center: PropTypes.arrayOf(PropTypes.number).isRequired,
        zoomRange: PropTypes.arrayOf(PropTypes.number).isRequired
    }).isRequired,
    setZoomSettings: PropTypes.func.isRequired
};

export {ZoomControls};
