import React, {useState, useCallback, useEffect} from 'react';
import {PropTypes} from 'prop-types';
import {useZoom} from '../zoom/use_zoom.js';
import {getMapHeight} from './function/get_map_height.js';
import {GeoMapProjectionMercator} from './projection/mercator.js';
import {useSize} from '../use_size.js';

const GeoMap = function(props){
    const [element, setElement] = useState(null);
    // Height and width start with 1px, because otherwise the zoomable map cannot calculate the zoom
    const [size, sizeRef] = useSize((w, h) => {return {width: w, height: h};}, {width: 1, height: 1});
    const elementRef = useCallback((element) => {
        setElement(element);
        sizeRef(element);
    }, [setElement, sizeRef]);
    const height = getMapHeight(size, props.height);
    // The hover state is maintained on the map globally. We can hover multiple dimensions at the time, for
    // example region and country or country and city. The layer should consume the hover state from its props
    // as it can be set from another layer, so it can't rely on its own internal state
    const [hover, setHover] = useState({});
    useEffect(() => {
        // call the onHover listner of the parent when the hover property changes
        props.onHover(hover);
    // eslint-disable-next-line
    }, [hover]);
    // create a refference for this map instance that will not change accross render cycles
    const [hoverTimers] = useState({});
    const setHoverDimension = (dimension, value) => {
        if(hoverTimers[dimension]){
            clearTimeout(hoverTimers[dimension]);
            hoverTimers[dimension] = null;
        }
        if(value === false){
            // wait for a moment before disabling the hover, it might be turned to active again. This happens
            // for example when moving in an child element.
            hoverTimers[dimension] = setTimeout(() => {
                setHover((prevHover) => {
                    const h = {...prevHover};
                    delete h[dimension];
                    return h;
                });
            }, 500);
        }else{
            setHover((prevHover) => {
                const h = {...prevHover, [dimension]: value};
                return h;
            });
        }
    };
    useEffect(() => (
        // cleanup any hover timers after unmount
        () => {
            for(const dimension of Object.keys(hoverTimers)){
                if(hoverTimers[dimension]){
                    clearTimeout(hoverTimers[dimension]);
                    hoverTimers[dimension] = null;
                }
            }
        }
    ), []);

    const getZoomCenter = (settings) => {
        if(settings.center){
            return settings.center;
        }
        if(settings.bounds){
            return [
                0.5 * settings.bounds[0][0] + 0.5 * settings.bounds[1][0],
                0.5 * settings.bounds[0][1] + 0.5 * settings.bounds[1][1]
            ];
        }
        return [0, 0];
    };
    const zoomSettings = {
        enabled: true,
        enableScrollWheel: true,
        enableControls: true,
        enablePanning: true,
        zoomRange: [1, 7],
        bounds: [[-180, 90], [180, -90]],
        margin: {top: 0, right: 0, bottom: 0, left: 0},
        level: 1,
        center: getZoomCenter(props.zoom),
        onZoom: () => {},
        onPan: () => {},
        // injecting zoom settings provided in the props
        ...(typeof props.zoom === 'object' ? props.zoom : {enabled: false}),
        width: size.width,
        height,
        Projection: props.Projection
    };

    const zoom = useZoom(zoomSettings, element ?? null);

    const renderLayer = (LayerComponent, index) => (
        <LayerComponent key={index}
                zoom={zoom}
                Projection={props.Projection}
                hover={hover}
                setHover={setHoverDimension}/>
    );

    return <div style={{position: 'relative'}} className={props.className} ref={elementRef}>

        <svg width={size.width + 'px'}
                height={(height) + 'px'}
                viewBox={`0 0 ${size.width} ${height}`}>
            {props.getSvgDefs(zoom)}
            <g>
                {props.layers.map(renderLayer)}
            </g>
        </svg>

        {props.htmlLayers.map(renderLayer)}

        {zoom.controls}

    </div>;
};

GeoMap.propTypes = {
    zoom: PropTypes.oneOfType([
        PropTypes.bool,
        PropTypes.shape({
            enabled: PropTypes.bool,
            enableScrollWheel: PropTypes.bool,
            enableControls: PropTypes.bool,
            enablePanning: PropTypes.bool,
            zoomRange: PropTypes.arrayOf(PropTypes.number),
            bounds: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.number)),
            margin: PropTypes.shape({
                top: PropTypes.number.isRequired,
                right: PropTypes.number.isRequired,
                bottom: PropTypes.number.isRequired,
                left: PropTypes.number.isRequired
            }),
            level: PropTypes.number,
            center: PropTypes.arrayOf(PropTypes.number),
            onZoom: PropTypes.func,
            onPan: PropTypes.func
        })
    ]),
    layers: PropTypes.arrayOf(PropTypes.elementType).isRequired,
    htmlLayers: PropTypes.arrayOf(PropTypes.elementType),
    className: PropTypes.string,
    height: PropTypes.oneOfType([PropTypes.number, PropTypes.func, PropTypes.string]),
    Projection: PropTypes.func,
    onHover: PropTypes.func,
    getSvgDefs: PropTypes.func
};

GeoMap.defaultProps = {
    zoom: false,
    className: 'zol-geo-map',
    height: '100%',
    Projection: GeoMapProjectionMercator,
    onHover: (hover) => {},
    htmlLayers: [],
    getSvgDefs: (zoom) => (null)
};

export {GeoMap};
