import React from 'react';
import {PropTypes} from 'prop-types';
import {useData} from '../../model/use_data.js';
import {useStateContext} from '../../helpers/state-context.js';
import {ItemListDiamond, diamondColorMap} from './diamond.js';
import {ItemListDiamondMobile} from './diamond-mobile.js';

function ItemList(props){
    const {getItem} = useData();
    const [{selected, hovered}, dispatch] = useStateContext(['selected', 'hovered']);

    if(selected !== null && props.isMobile){
        return null;
    }

    const items = getItem(props.type) || [];
    const selectItem = (index) => {
        if(selected?.index === index + 1){
            dispatch({type: 'selected', value: null});
        }else{
            dispatch({type: 'selected', value: {type: props.type, index: index + 1}});
        }
    };
    const hoverItem = (index) => {
        if(! selected && index === null){
            dispatch({type: 'hovered', value: null});
        }else if(! selected){
            dispatch({type: 'hovered', value: {type: props.type, index: index + 1}});
        }
    };

    const colors = Object.keys(diamondColorMap);
    return <div className={'zol-cpa-map-col zol-cpa-map-' + props.type} ref={props.reference}>
        <h2>{getItem(props.type + '-title')}</h2>
        {items.map((item, index) => {
            const isActive = (selected?.index === index + 1 && selected?.type === props.type) ||
                    (props.type === 'recs' && hovered && hovered.type === 'iso3' &&
                    item.countries.includes(hovered.value));
            return <div key={item['title-short']}
                    className={'zol-cpa-list-item' +
                        (isActive ? ' zol-active' : (selected ? ' zol-inactive' : ''))}
                    onClick={() => {selectItem(index);}}
                    onMouseOver={() => {hoverItem(index);}}
                    onMouseOut={() => {hoverItem(null);}}>
                {props.isMobile ?
                    <ItemListDiamondMobile type={props.type}/> :
                    <ItemListDiamond color={colors[index % colors.length]} isActive={isActive}/>
                }
                <span>{item['title-short']}</span>
            </div>;
        })}
    </div>;
};

ItemList.propTypes = {
    type: PropTypes.oneOf(['unions', 'recs']).isRequired,
    isMobile: PropTypes.bool.isRequired,
    reference: PropTypes.func
};

export {ItemList};
