import {ModelXhr} from '../../model/xhr.js';
import {CONFIG} from '../../../config.js';

class GeoMapModelCountry extends ModelXhr {
    static modelName = 'GeoMapModelCountry';
    static initiated = false;
    static modelResult = null;

    constructor(selectData){
        super(selectData, {
            endpoint: CONFIG.static_root + 'data/map-details.json',
            method: 'GET'
        });
        this.name = 'GeoMapModelCountry';

        // this enables caching
        this.cacheEnabled = true;
        this.cacheLimit = 1;
    }

    getDataFromResponse(data){
        try {
            const json = JSON.parse(data);
            return this.processCountryData(json);
        } catch (e){
            const error = 'Error loading map data: ' + e.message;
            this.onFailure(error);
        }
        return null;
    }

    processCountryData(data){
        const countries = data.countries.map((item) => {
            // sanitize country result
            return {
                iso2: item.id2,
                iso3: item.id3,
                country: item.c,
                region_id: item.r,
                region: data.regions[item.r],
                subregion_id: item.s,
                subregion: data.regions[item.s],
                bbox: item.b,
                centroid: item.l,
                area: this.getBBoxArea(item.b),
                population: item.p
            };
            // store it by iso3 code
        }).reduce((obj, item) => {obj[item.iso3] = item; return obj;}, {});

        // make a copy stored by iso2 code
        const countriesIso2 = Object.values(countries)
                .reduce((obj, item) => {obj[item.iso2] = item; return obj;}, {});

        return {
            regions: data.regions,
            subregions: data.subregions,
            countries,
            countriesIso2
        };
    }

    getBBoxArea(bbox){
        return (bbox[1][0] - bbox[0][0]) * (bbox[1][1] - bbox[0][1]);
    }
}

export {GeoMapModelCountry};
