
const getProjectedBounds = function(bounds, Projection){
    const imageBounds = [[Infinity, Infinity], [-Infinity, -Infinity]];
    const testPoint = (x, y) => {
        const p = Projection.apply([x, y]);
        if(p[0] < imageBounds[0][0]){
            imageBounds[0][0] = p[0];
        }
        if(p[0] > imageBounds[1][0]){
            imageBounds[1][0] = p[0];
        }
        if(p[1] < imageBounds[0][1]){
            imageBounds[0][1] = p[1];
        }
        if(p[1] > imageBounds[1][1]){
            imageBounds[1][1] = p[1];
        }
    };
    for(let i = 0; i <= 12; i++){
        const x = bounds[0][0] + i * (bounds[1][0] - bounds[0][0]) / 20;
        const y = bounds[0][1] + i * (bounds[1][1] - bounds[0][1]) / 20;
        testPoint(x, bounds[0][1]);
        testPoint(x, bounds[1][1]);
        testPoint(bounds[0][0], y);
        testPoint(bounds[1][0], y);
    }
    return imageBounds;
};

export {getProjectedBounds};
