import {memo} from './functions/memo.js';
import {CONFIG} from '../config.js';

// Here we configure multiple lists of color combinations. We start with palettes with the least amount of
// colors and go up from there. If we need a palette we call getColorPalette, which will choose the most
// appropriate list (the one that covers the options required, but as little as possible). If we request a
// palette with more colors that the largest list here, we will interpolate between these options to get
// all the colors we need. All colors here are rgb values.
const palettes = CONFIG.colorPalettes || [
    [[91, 146, 229], [255, 184, 28]],

    [[91, 146, 229], [255, 103, 31], [255, 184, 28], [92, 184, 178]],

    [[91, 146, 229], [210, 38, 48], [255, 103, 31], [255, 184, 28], [92, 184, 178]],

    [[91, 146, 229], [0, 51, 160], [152, 54, 178], [210, 38, 48], [255, 103, 31], [255, 184, 28],
        [205, 220, 58], [92, 184, 178]],

    [[1, 188, 214], [46, 190, 255], [91, 146, 229], [0, 51, 160], [104, 58, 183], [152, 54, 178],
        [210, 15, 119], [210, 38, 48], [255, 103, 31], [255, 184, 28], [255, 236, 60], [205, 220, 58],
        [139, 195, 73], [36, 155, 35], [92, 184, 178]]
];

export const getColorPalette = function(amount, customPalettes = null){
    const selectedPalettes = (customPalettes === null) ? palettes : customPalettes;
    let index = 0;
    for(const palette of selectedPalettes){
        index++;
        if(palette.length < amount && index < selectedPalettes.length){
            continue;
        }

        const step = palette.length / amount;
        let offset = 0;
        const result = [];
        for(let i = 0; i < amount; i++){
            const position = i * step + offset;
            const s = Math.floor(position);
            let f = position - s;
            offset = 0;

            // if close enough to palette colour 'snap' to it.
            if(step >= 1){
                f = Math.round(f);
            }else{
                if(f < 0.5 * step){
                    offset = -0.5 * f;
                    f = 0;
                }else if(1 - f < 0.5 * step){
                    offset = 0.5 * (1 - f);
                    f = 1;
                }
            }
            if(s + 1 >= palette.length){
                result.push([
                    palette[s][0],
                    palette[s][1],
                    palette[s][2]
                ]);
            }else{
                result.push([
                    Math.round((1 - f) * palette[s][0] + f * palette[s + 1][0]),
                    Math.round((1 - f) * palette[s][1] + f * palette[s + 1][1]),
                    Math.round((1 - f) * palette[s][2] + f * palette[s + 1][2])
                ]);
            }
        }
        return result;
    }
};

