import React from 'react';
import {PropTypes} from 'prop-types';
import colors from '../../assets/variables.module.scss';

const diamondColorMap = {
    green: colors.colorGreen,
    purple: colors.colorPurple,
    orange: colors.colorOrange,
    pink: colors.colorPink,
    yellow: colors.colorYellow
};

const ItemListDiamond = function(props){
    const cssColor = diamondColorMap[props.color];
    const cssColorActive = props.isActive ? 'white' : cssColor;

    return <svg width="76" height="77" viewBox="0 0 76 77">
        <g transform="translate(2.09 2.641)">
            <g transform="translate(36.961 0.359) rotate(47)" fill="none" stroke={cssColorActive}
                    strokeLinejoin="round" strokeWidth="11">
                <rect width="50.047" height="50.047" stroke="none"/>
                <rect x="5.5" y="5.5" width="39.047" height="39.047" fill="none"/>
            </g>
            {props.isActive ?
                <g transform="translate(36.1 21.2) rotate(47)" fill={cssColor}>
                    <rect width="20" height="20" stroke="none"/>
                </g> :
                null
            }
            <g transform="translate(31.392 0.43) rotate(38)" fill="none" stroke={colors.colorHighlight}
                    strokeLinejoin="round" strokeWidth="2">
                <rect width="50.29" height="50.29" stroke="none"/>
                <rect x="1" y="1" width="48.29" height="48.29" fill="none"/>
            </g>
            <g transform="translate(36.973) rotate(47)" fill="none" stroke={colors.colorHighlight}
                    strokeLinejoin="round" strokeWidth="2">
                <rect width="50.555" height="50.555" stroke="none"/>
                <rect x="1" y="1" width="48.555" height="48.555" fill="none"/>
            </g>
        </g>
    </svg>;
};

ItemListDiamond.propTypes = {
    color: PropTypes.oneOf(Object.keys(diamondColorMap)),
    isActive: PropTypes.bool
};

ItemListDiamond.defaultProps = {
    color: Object.keys(diamondColorMap)[0],
    isActive: false
};

export {ItemListDiamond, diamondColorMap};
