import React from 'react';
/* react-dom/client is not a JS file, hence no extension */
/* eslint-disable-next-line */
import ReactDOM from 'react-dom/client';
import './index.scss';
import {App} from './App.js';
import {reportWebVitals} from './reportWebVitals.js';
import {StateContext} from './helpers/state-context/state-context.js';
import {EmbeddedApp} from './helpers/embed/embedded-app.js';
import {CONFIG} from './config.js';

const scripts = document.getElementsByTagName('script');
const path = scripts[0].src;
// JS file is loaded from root/static/js/scriptname.js. Removing three parts will leave us with the root
const scriptPath = path.split('/').slice(0, -3).join('/') + '/';

const iframeUrl = window.hasOwnProperty('infographicIframeLocation') ?
    window.infographicIframeLocation :
    (scriptPath + 'embed.html');

const initialState = {
    selected: null,
    hovered: null
};

const component = <React.StrictMode>
    <StateContext initialState={initialState}>
        <App/>
    </StateContext>
</React.StrictMode>;

if(window.infographicLaunchDirectly){
    // We are not using an embedded version but want to render this thing directly
    const root = ReactDOM.createRoot(document.getElementById('charter-project-infographic'));
    root.render(component);
}else{
    const app = new EmbeddedApp(
            window.infographicContainer || document.getElementById('charter-project-infographic'),
            iframeUrl,
            {
                enableShadowDom: false,
                defaultHeight: window.infographicDefaultHeight || 600,
                config: {...CONFIG},
                onReceive: (data) => {
                    // initialize the app after the configuration has been sent and received
                    if(data.type === 'start'){
                        initApp();
                    }
                }
            });

    const initApp = () => {
        app.init((parentElement) => {
            const root = ReactDOM.createRoot(parentElement);
            root.render(component);
            return () => {
                root.unmount();
            };
        });
    };
}

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
