import {defaultOptions, addMeridian, rad, sin, deg} from './functions.js';

/**
 * This provides the Mercator projection for a map. Code inspired by projects package from Juliuste:
 * https://github.com/juliuste/projections
 */
class GeoMapProjectionMercator {
    static apply(coordinateInput, options = {}){
        options = {...defaultOptions, ...options};
        const coordinate = [...addMeridian(coordinateInput, options.meridian)];

        // Make sure the latitude is not larger than the latLimit. This projection is asymptotic towards 90deg
        // in other words going to infinity while nearing to 90deg
        const f = Math.abs(coordinate[1]) / options.latLimit;
        coordinate[1] = f > 1 ? coordinate[1] / f : coordinate[1];

        return [
            options.coordinateScale * (rad(coordinate[0]) / (2 * Math.PI) + 0.5),

            options.coordinateScale *
                ((Math.atanh(sin(options.latLimit)) - Math.atanh(sin(coordinate[1]))) / (2 * Math.PI))
        ];
    }

    static invert(point, options = {}){
        options = {...defaultOptions, ...options};

        const result = [
            deg((2 * (point[0] / options.coordinateScale) - 1) * Math.PI),

            deg(Math.asin(Math.tanh(
                    Math.atanh(h.sin(options.latLimit)) - 2 * Math.PI * (point[1] / options.coordinateScale)
            )))
        ];
        return addMeridian(result, -1 * options.meridian);
    }
};

export {GeoMapProjectionMercator};
