import React from 'react';
import {useData} from '../../model/use_data.js';
import {ReactComponent as LogoCPA} from '../../assets/images/logo-cpa.svg';
import LogoEU from '../../assets/images/eu.png';

// TODO: Needs to be updated for Global Care Portal
function ViewFooter(props){
    const {getItem} = useData();

    return <div className="zol-cpa-footer">
        <span className="zol-cpa-logo">
            <LogoCPA/>
        </span>
        <span className="zol-cpa-footer-text">
            {getItem('footer-text')}
            <br/>
            <img src={LogoEU} alt="Co-funded by the European Union"/>
        </span>
    </div>;
}

export {ViewFooter};
