import React from 'react';
import {PropTypes} from 'prop-types';
import {useSize} from '../../helpers/use_size.js';
import {useModel} from '../../helpers/model/use_model.js';
import {GeoMapModelCountry} from '../../helpers/geo-map/model/country.js';

const LayerTooltip = function(props){
    // Load the data for the map
    const iso3 = props.hover.country_iso3;
    const resultSet = useModel(GeoMapModelCountry);
    const [size, measuredRef] = useSize();

    const country = resultSet.status === GeoMapModelCountry.Status.SUCCESS ?
            resultSet.data.countries[iso3] : null;
    const tooltipText = props.getTooltipText(iso3);

    if(! tooltipText || ! country){
        return null;
    }

    const tooltipTitle = props.getTooltipTitle(iso3, country.country);

    const point = props.zoom.getPointFromGeolocation(country.centroid);

    const style = {
        position: 'absolute',
        top: point[1]
    };
    let className = 'zol-map-tooltip-right';
    if(point[0] + 200 < size.width){
        style.left = point[0];
    }else{
        style.right = size.width - point[0];
        className = 'zol-map-tooltip-left';
    }
    const otherProps = {
        className: 'zol-map-tooltip ' + className,
        onMouseOver: () => {props.setHover('country_iso3', iso3);},
        onMouseOut: () => {props.setHover('country_iso3', false);},
        onClick: () => {
            if(props.zoom.validateClick()){
                props.onClick(iso3);
                props.setHover('country_iso3', false);
            }
        }
    };

    // We span a div around our layer to get its size. However we don't want to block the layers underneith
    // (they would not receive mouse over events), so we keep the height 0 (we only need the width of the
    // layer).
    const styleOuter = {
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0
    };

    return <div ref={measuredRef} style={styleOuter}>
        <div style={style} {...otherProps}>
            <h2>{tooltipTitle}</h2>
            <div>{tooltipText}</div>
        </div>
    </div>;
};

LayerTooltip.propTypes = {
    zoom: PropTypes.object.isRequired,
    Projection: PropTypes.func.isRequired,
    getTooltipText: PropTypes.func.isRequired,
    getStyle: PropTypes.func,
    hover: PropTypes.object,
    setHover: PropTypes.func,
    onClick: PropTypes.func,
    getTooltipTitle: PropTypes.func
};

LayerTooltip.defaultProps = {
    /* Prevent using opacity/transparency on the map as this has a negative impact on performance */
    getStyle: (iso3, hoverState, zoomScale) => {},
    onHover: (iso3, isActive) => {},
    onClick: (iso3) => {},
    hover: {},
    setHover: (dimension, value) => {},
    getTooltipTitle: (iso3, countryName) => (countryName)
};

export {LayerTooltip};
