import React from 'react';
import {ModelData} from './data.js';
import {useModel} from '../helpers/model/use_model.js';

const useData = () => {
    const [data, setData] = React.useState({});
    const resultSet = useModel(ModelData);

    React.useEffect(() => {
        if(resultSet.status === ModelData.Status.SUCCESS){
            setData(resultSet.data || {});
        }else if(resultSet.status === ModelData.Status.FAILED){
            console.error('Failed to load data: ' + resultSet.error);
        }
    }, [resultSet]);

    const getItem = (key) => (data[key] || null);
    const isDataReady = Object.keys(data).length > 0;

    return {getItem, data, isDataReady};
};

export {useData};
