// delta contains the change in position (x and y in pixels) and the relative zoom change relative to the

// current view. This needs to be translated to a transformation on the source image
const getTransformationCallback = function(delta, animationDuration, cursorScreenLocation){
    return (currentZoom) => {
        if(Object.keys(delta).length === 0){
            return {...currentZoom};
        }
        const newZoom = {level: currentZoom.level, center: currentZoom.center.slice()};
        let scale = 1;
        if(delta.z){
            newZoom.level *= delta.z;
            if(newZoom.level < currentZoom.zoomRange[0]){
                newZoom.level = currentZoom.zoomRange[0];
            }else if(newZoom.level > currentZoom.zoomRange[1]){
                newZoom.level = currentZoom.zoomRange[1];
            }
            scale = newZoom.level / currentZoom.level;

            if(cursorScreenLocation && ! delta.x && ! delta.y){
                const mapPosition = currentZoom.getElementScreenPosition();
                const cursorLocation = [
                    cursorScreenLocation[0] - mapPosition[0],
                    cursorScreenLocation[1] - mapPosition[1]
                ];

                // calculate the cursor location relative to the center
                const location = [
                    cursorLocation[0] - 0.5 * currentZoom.width,
                    cursorLocation[1] - 0.5 * currentZoom.height
                ];

                // translate to keep the same point of the source image under the cursor when zooming in/out
                if(scale >= 1){
                    delta.x = ((1 / scale) - 1) * location[0];
                    delta.y = ((1 / scale) - 1) * location[1];
                }else{
                    delta.x = location[0];
                    delta.y = location[1];
                }
            }
        }
        const centerProjected = currentZoom.Projection.apply(currentZoom.center);
        const x = delta.x ? centerProjected[0] - delta.x / currentZoom.scale : centerProjected[0];
        const y = delta.y ? centerProjected[1] - delta.y / currentZoom.scale : centerProjected[1];

        newZoom.center = viewBoxInBounds(x, y, currentZoom.width, currentZoom.height, currentZoom.bounds,
                currentZoom.scale * scale, currentZoom.Projection);

        return {...currentZoom, ...newZoom};
    };
};

// Make sure we can't pan the image out of the bounds
const viewBoxInBounds = function(x, y, width, height, bounds, scale, Projection){
    const viewBounds = [
        Projection.inverse([
            x - 0.5 * width / scale,
            y - 0.5 * height / scale
        ]),
        Projection.inverse([
            x + 0.5 * width / scale,
            y + 0.5 * height / scale
        ])
    ];

    [x, y] = Projection.inverse([x, y]);

    // Make sure the lowest value is first and the largest is second for both boxes
    const vb = [
        [
            Math.min(viewBounds[0][0], viewBounds[1][0]),
            Math.min(viewBounds[0][1], viewBounds[1][1])
        ],
        [
            Math.max(viewBounds[0][0], viewBounds[1][0]),
            Math.max(viewBounds[0][1], viewBounds[1][1])
        ]
    ];
    const tb = [
        [
            Math.min(bounds[0][0], bounds[1][0]),
            Math.min(bounds[0][1], bounds[1][1])
        ],
        [
            Math.max(bounds[0][0], bounds[1][0]),
            Math.max(bounds[0][1], bounds[1][1])
        ]
    ];

    // Note that it can occur that both sides are out bound. In that case we need to center x between the 2
    // bounds
    if(vb[0][0] < tb[0][0]){
        if(vb[1][0] > tb[1][0]){
            // both edges out of bound, center x
            x = tb[0][0] + 0.5 * (tb[1][0] - tb[0][0]);
        }else{
            x += tb[0][0] - vb[0][0];
        }
    }else if(vb[1][0] > tb[1][0]){
        x += tb[1][0] - vb[1][0];
    }
    if(vb[0][1] < tb[0][1]){
        if(vb[1][1] > tb[1][1]){
            // both edges out of bound, center y
            y = tb[0][1] + 0.5 * (tb[1][1] - tb[0][1]);
        }else{
            y += tb[0][1] - vb[0][1];
        }
    }else if(vb[1][1] > tb[1][1]){
        y += tb[1][1] - vb[1][1];
    }
    return [x, y];
};

export {getTransformationCallback};
