import React from 'react';
import {PropTypes} from 'prop-types';
import {diamondColorMap} from '../item-list/diamond.js';
import colors from '../../assets/variables.module.scss';

const DetailsBackground = function(props){
    const width = props.size.width;
    const height = props.size.height;
    const path = [
        [5, 45], [30, 25],
        [props.arrowLeft - 20, 25], [props.arrowLeft, 5], [props.arrowLeft + 20, 25],
        [width - 5, 25],
        [width - 5, height - 30], [width - 35, height],
        [0.15 * width, height - 20], [5, height - 20]
    ];
    const cssColor = diamondColorMap[props.color];
    return <svg width={width} height={height} viewBox={[0, 0, width, height].join(' ')}>
        <path d={'M' + path.map((c) => (c.join(','))).join('L') + 'Z'} fill={colors.colorWhite}/>
        <path d={'M' + path.map((c) => (c.map((p) => (p + 1)).join(','))).join('L') + 'Z'}
                fill="none" stroke={cssColor} strokeWidth="10" opacity="0.64"/>
    </svg>;
};

DetailsBackground.propTypes = {
    size: PropTypes.shape({width: PropTypes.number.isRequired, height: PropTypes.number.isRequired}),
    arrowLeft: PropTypes.number,
    color: PropTypes.string
};

DetailsBackground.defaultProps = {
    arrowLeft: 50
};

export {DetailsBackground};
