import React from 'react';
import PropTypes from 'prop-types';

function GeoMapLayerBackground(props){
    return (
        <g className={props.className}>
            <rect x={0}
                    y={0}
                    width={props.zoom.width + props.zoom.margin.left + props.zoom.margin.right}
                    height={props.zoom.height + props.zoom.margin.top + props.zoom.margin.bottom}
                    style={props.getStyle(props.zoom.scale)}
                    onMouseOver={() => {props.setHover('country_iso3', false);}}
                    onClick={() => {
                        if(props.zoom.validateClick()){
                            props.onClick();
                            props.setHover('country_iso3', false);
                        }
                    }}/>
        </g>
    );
}

GeoMapLayerBackground.propTypes = {
    zoom: PropTypes.object.isRequired,
    Projection: PropTypes.func.isRequired,
    getStyle: PropTypes.func,
    hover: PropTypes.object,
    setHover: PropTypes.func,
    onClick: PropTypes.func,
    className: PropTypes.string
};

GeoMapLayerBackground.defaultProps = {
    /* Prevent using opacity/transparency on the map as this has a negative impact on performance */
    getStyle: (zoomScale) => {},
    onClick: () => {},
    hover: {},
    setHover: (dimension, value) => {},
    className: 'zol-geo-map-layer-background'
};

export {GeoMapLayerBackground};
