import {defaultOptions, addMeridian, rad, sin, deg} from './functions.js';

/**
 * The Miller cylindrical projection, inspired by the projections by Juliuste:
 * https://github.com/juliuste/projections
 */
class GeoMapProjectionMiller {
    static apply(coordinate, options = {}){
        options = {...defaultOptions, ...options};
        coordinate = addMeridian(coordinate, options.meridian);

        return [
            options.coordinateScale * (rad(coordinate[0]) / (2 * Math.PI) + 0.5),

            options.coordinateScale * (
                (Math.atanh(sin(options.latLimit * 4 / 5)) - Math.atanh(sin(coordinate[1] * 4 / 5))) /
                    (2 * Math.PI) * (5 / 4)
            )
        ];
    }

    static inverse(point, options = {}){
        options = {...defaultOptions, ...options};

        const result = [
            deg((2 * point[0] / 1000 - 1) * Math.PI),

            deg((5 / 4) * Math.asin(Math.tanh((
                Math.atanh(sin(options.latLimit * 4 / 5)) -
                8 * Math.PI * (point[1] / options.coordinateScale) / 5
            ))))
        ];

        return addMeridian(result, -1 * options.meridian);
    };
}

export {GeoMapProjectionMiller};
