import {parseCSSColor} from '../../helpers/color.js';
import colors from '../../assets/variables.module.scss';
import {useCountryData} from './use_country_data.js';

const useColors = function(item){
    const {isInFilter, africaFilter} = useCountryData();

    const getCountryColor = (iso3, hoverState) => {
        if(isInFilter(iso3, africaFilter)){
            // We could use opacity for this, but this has quite a negative impact on performance.
            const rgb1 = parseCSSColor(colors.colorMap);
            const rgb2 = parseCSSColor(colors.colorDark);
            return 'rgb(' +
                Math.round(0.6 * rgb1[0] + 0.4 * rgb2[0]) + ', ' +
                Math.round(0.6 * rgb1[1] + 0.4 * rgb2[1]) + ', ' +
                Math.round(0.6 * rgb1[2] + 0.4 * rgb2[2]) +
                ')';
        }else{
            // We could use opacity for this, but this has quite a negative impact on performance.
            const rgb1 = parseCSSColor(colors.colorWhite);
            const rgb2 = parseCSSColor(colors.colorDark);
            return 'rgb(' +
                Math.round(0.4 * rgb1[0] + 0.6 * rgb2[0]) + ', ' +
                Math.round(0.4 * rgb1[1] + 0.6 * rgb2[1]) + ', ' +
                Math.round(0.4 * rgb1[2] + 0.6 * rgb2[2]) +
                ')';
        }
    };

    return {getCountryColor};
};

export {useColors};
