import {GeoMapModelCountry} from '../../helpers/geo-map/model/country.js';
import {useModel} from '../../helpers/model/use_model.js';

const useCountryData = function(){
    const resultSet = useModel(GeoMapModelCountry);
    const countries = resultSet.data?.countries || {};

    const getCountryData = resultSet.status === GeoMapModelCountry.Status.SUCCESS ?
            (iso3) => (countries[iso3] || null) :
            (iso3) => (null);

    const isInFilter = (iso3, filter) => {
        const value = getCountryData(iso3) || {};
        return filter.some((f) => (
            Object.entries(f).every(([dim, val]) => (value[dim] && value[dim] === val))
        ));
    };

    const africaFilter = ['LBY', 'xHT', 'xMS', 'xAB', 'xIT']
            .map((item) => {return {iso3: item};});
    africaFilter.push({region_id: 'RAF'});

    return {getCountryData, isInFilter, africaFilter};
};

export {useCountryData};
