import {CONFIG} from '../../config.js';
import {GeoMapModelTopoJson} from '../../helpers/geo-map/model/topo-json.js';
import topojsonFeature from '../../helpers/vendor/topojson/feature.js';
import topojsonTopology from '../../helpers/vendor/topojson-server/topology.js';
import topojsonMerge from '../../helpers/vendor/topojson/merge.js';
import {createPath} from '../../helpers/geo-map/function/create_path.js';

class ModelContour extends GeoMapModelTopoJson {
    static modelName = 'ModelContour';

    constructor(selectData){
        super(selectData, {
            endpoint: CONFIG.static_root + 'data/zolabo-map.json',
            method: 'GET'
        });
        this.name = 'ModelContour';
    }

    // Process the country paths. Call this when Model is in state success and this.data is set
    processCountryPaths(data, getItemDetails, filter){
        if(! this.selectData?.Projection || typeof this.selectData.Projection.apply !== 'function'){
            console.error('No projection provided in selection criteria for GeoMapModelTopoJson');
            return null;
        }

        const countries = topojsonFeature(data, data.objects[this.featuresName]);
        if(countries.type === 'Feature' && ! countries.features){
            countries.features = [{id: this.featureName, geometry: countries.geometry}];
        }

        // Extract filtered countries and merge them into a single shape
        const filteredCountries = this.getFilteredFeatures(countries.features, getItemDetails, filter);

        // get the bounding box of the filtered countries
        const bounds = this.getCountryBounds(filteredCountries, getItemDetails);

        const filteredTopoJSON = topojsonTopology({
            countries: {type: 'FeatureCollection', features: filteredCountries}
        });
        const continent = topojsonMerge(filteredTopoJSON, filteredTopoJSON.objects.countries.geometries);

        const countryPaths = [];
        if(continent.coordinates){
            const c = {type: 'Feature', id: 'CONTOUR', properties: {}};
            c.path = createPath(continent.coordinates, this.selectData.Projection);
            countryPaths.push(c);
        }

        this.processedData = {countryPaths, bounds};
        return {countryPaths, bounds};
    }
}

export {ModelContour};
