import React, {useState} from 'react';
import {Map} from './components/map/index.js';
import {ItemList} from './components/item-list/index.js';
import {Timeline} from './components/timeline/timeline.js';
import {ViewFooter} from './view/footer/footer.js';
import {ViewHeader} from './view/header/index.js';
import {Details} from './components/details/index.js';
import {useSize} from './helpers/use_size.js';
import './index.scss';
import {useStateContext} from './helpers/state-context.js';

function App(){
    const [width, measuredRef] = useSize((w, h) => (w), 200);
    const isMobile = width < 600;
    const [headerHeight, setHeaderHeight] = useState(200);
    const [mapHeight, setMapHeight] = useState(600);
    const [col1Height, measuredCol1Ref] = useSize((w, h) => (h || 200), 200);
    const [col2Height, measuredCol2Ref] = useSize((w, h) => (h || 200), 200);
    const detailsHeight = Math.max(col1Height, col2Height);
    const [bottomPosition, setBottomPosition] = useState(400);
    const marginTop = bottomPosition - (headerHeight + mapHeight);
    const contentHeight = Math.max(mapHeight, headerHeight + detailsHeight, headerHeight + bottomPosition);
    const [modalPosition, setModalPosition] = useState([0, 0]);
    const [, dispatch] = useStateContext(['selected']);

    return <div ref={measuredRef} className="zol-cpa-root">
        {isMobile ? <ViewHeader isMobile={true} onHeightChange={(h) => {setHeaderHeight(h);}}/> : null}

        <Map isMobile={isMobile} headerHeight={headerHeight} modalPosition={modalPosition}
                setModalPosition={setModalPosition}
                onHeightChange={(h) => {setMapHeight(h);}}/>

        {isMobile ? null : <ViewHeader isMobile={false} onHeightChange={(h) => {setHeaderHeight(h);}}/>}

        <div className="zol-cpa-map-row">
            <ItemList type="unions" isMobile={isMobile} reference={measuredCol1Ref}/>
            <ItemList type="recs" isMobile={isMobile} reference={measuredCol2Ref}/>
            <Details position={[modalPosition[0], modalPosition[1] + (isMobile ? 1 : -1) * headerHeight]}
                    setBottonPosition={setBottomPosition}
                    isMobile={isMobile}/>
        </div>

        <div className="zol-cpa-map-bottom"
                onClick={() => {dispatch({type: 'selected', value: null});}}
                style={{top: contentHeight, marginTop: isMobile && marginTop > 0 ? marginTop : 0}}>
            <Timeline isMobile={isMobile}/>
            <ViewFooter/>
        </div>

    </div>;
}

export {App};
